import Li from '@smart-react-components/core/Element/Li';
import styled from 'styled-components';
export default styled(Li)(({ theme, isNested, onClick }) => `
  display: flex;
  justify-content: space-between;

  ${onClick
    ? `
      cursor: pointer;  
    `
    : ''}

  > a,
  > span {
    color: ${theme.$.color.dynamic.asideFont};
    cursor: pointer;
    font-size: 13px;
    font-weight: ${theme.$.fontWeight.semibold};
    padding: 8px 0 8px ${isNested ? '40px' : '30px'};
    position: relative;
    transition: color 150ms ease-in-out 0s;

    &:hover,
    &.active {
      color: ${theme.$.color.blue};
      font-weight: ${theme.$.fontWeight.bold};

      + svg {
        fill: ${theme.$.color.blue};
      }
    }

    &.active + svg {
      transform: rotate(90deg);
    }
  }

  > a.active::before {
    background: ${theme.$.color.blue};
    border-radius: 12px;
    bottom: 0;
    content: '';
    left: -3px;
    position: absolute;
    top: 0;
    width: 6px;
  }

  > svg {
    fill: ${theme.$.color.dynamic.asideFont};
    margin-right: 30px;
    transition: 150ms ease-in-out 0s;
    width: 16px;
  }
`);
