import { loadModulesOnClient } from '@smart-react-components/router';
import ClientRouter from '@smart-react-components/router/ClientRouter';
import React from 'react';
import { createRoot } from 'react-dom/client';
import { Provider } from 'react-redux';
import App from './components/App';
import NotFound from './containers/NotFound';
import routes from './routes';
import createStore from './redux';
loadModulesOnClient(routes)
    .then(() => {
    const store = createStore();
    let isDark = false;
    const match = document.cookie.match(new RegExp('(^| )isDark=([^;]+)'));
    if (match) {
        isDark = match[2] === 'true';
    }
    const root = createRoot(document.getElementById('app'));
    root.render(React.createElement(Provider, { store: store },
        React.createElement(ClientRouter, { fallback: React.createElement(NotFound, null), isHashRouter: true, routes: routes },
            React.createElement(App, { isDark: isDark }))));
});
